import React, { useEffect, useState } from 'react';
import LayoutPage from '../Layout/layout';
import firebase from '../firebase';
import { NavLink } from 'react-router-dom';

const ResultTest = () => {
    const [todoList, setTodoList] = useState([]);
    const [listResult, setListResult] = useState([]);
    const [test, setTest] = useState({});
    
    useEffect(() => {
        const getProduct = () => {
            const todoRef = firebase.database().ref('test');
            todoRef.on('value', (snapshot) => {
              const todos = snapshot.val();
              const data = [];
              for (let id in todos) {
                data.push({ id, ...todos[id] });
              }
              setTodoList(data);
      
            });
          }
          getProduct();
    },[])
    const Result = (key) => {
        let info = {baithi:key.baithi, socauhoi: key.socauhoi};
        setTest(info)
        const todoRef = firebase.database().ref('result').orderByChild('idbaithi').equalTo(key.id);
            todoRef.on('value', (snapshot) => {
              const todos = snapshot.val();
              const data = [];
              for (let id in todos) {
                data.push({ id, ...todos[id] });
              }
              setListResult(data);
      
            });
    }
    return(
        <LayoutPage>
            <h3>Kết quả bài thi</h3>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Tên bài thi</th>
            <th>Số câu hỏi</th>
            <th>#</th>
          </tr>
        </thead>
        <tbody>
          {todoList ? todoList.map((key, index) => (
            <tr>
              <td>{key.baithi}</td>
              <td>{key.socauhoi}</td>
              <td>
                <NavLink to={'#'}><button className="btn btn-secondary" data-toggle="modal" data-target="#myModal" onClick={() => Result(key)}>Điểm thi</button></NavLink>
              </td>
            </tr>
          )) : null}

        </tbody>
      </table>
      <div id="myModal" className="modal fade" role="dialog">
  <div className="modal-dialog">
    {/* Modal content*/}
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal">×</button>
        <h4 className="modal-title">Điểm bài thi: {test.baithi}</h4>
      </div>
      <div className="modal-body">
      <table className="table table-striped">
        <thead>
          <tr>
            <th>MSSV</th>
            <th>Họ tên</th>
            <th>Số câu đúng</th>
            <th>Điểm </th>
          </tr>
        </thead>
        <tbody>
          {listResult ? listResult.map((key, index) => (
            <tr>
              <td>{key.mssv}</td>
              <td>{key.hoten}</td>
              <td>{key.dung+'/'+test.socauhoi}</td>
              <td>{key.diem}</td>
            </tr>
          )) : null}

        </tbody>
      </table>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal">Đóng lại</button>
      </div>
    </div>
  </div>
</div>
        </LayoutPage>
    )
}
export default ResultTest;